  color: #0080ff;
}

.editor-wrapper .JSXElement.JSXText {
  color: darkgoldenrod;
}

.editor-wrapper .JSXElement.JSXGlyph {
  background: cyan;
  opacity: 0.25;
}

.editor-wrapper .JSXOpeningFragment.JSXBracket {
  color: #0080ff;
